.circleGraph {
    position: absolute;
    top: 0px;
    right: 0px;
    bottom: 0px;
    svg {
        transform: rotate(-90deg);
        circle {
            transition: stroke-dashoffset 0.8s ease;
        }
    }
    &::after {
        content: "";
        position: absolute;
        top: 30%;
        right: 30%;
        width: 40%;
        height: 40%;
        background-repeat: no-repeat;
        background-image: url(../../assets/pic/list.svg);
    }
}
