.st1 {
    font-size: 83.0285px;
}

.notFound {
    text-align: center;

    &__img {
        max-width: 500px;
        max-height: 400px;
        text-align: center;
        fill: #16a085;
    }

    &__circle {
        animation: out 2s infinite ease-out;
        fill: #16a085;
    }

    &__message {
        color: #16a085;

        &:after {
            content: "]";
        }

        &:before {
            content: "[";
        }

        &:after,
        &:before {
            color: #16a085;
            font-size: 20px;
            -webkit-animation-name: opacity;
            -webkit-animation-duration: 2s;
            -webkit-animation-iteration-count: infinite;
            -webkit-animation-name: opacity;
            animation-name: opacity;
            -webkit-animation-duration: 2s;
            animation-duration: 2s;
            -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
            margin: 0 50px;
        }
    }
}
path#XMLID_5_ {
    fill: #16a085;
    filter: url(#blurFilter4);
}
path#XMLID_11_,
path#XMLID_2_ {
    fill: #16a085;
}

@-webkit-keyframes opacity {
    0%,
    100% {
        opacity: 0;
    }
    50% {
        opacity: 1;
    }
}

@keyframes opacity {
    0%,
    100% {
        opacity: 0;
    }
    50% {
        opacity: 1;
    }
}

@keyframes out {
    0% {
        r: 1;
        opacity: 0.9;
    }
    25% {
        r: 5;
        opacity: 0.3;
    }
    50% {
        r: 10;
        opacity: 0.2;
    }
    75% {
        r: 15;
        opacity: 0.1;
    }
    100% {
        r: 20;
        opacity: 0;
    }
}
